/**
 * Google Material Design style card depth
 * Can receive an int between -5 and 5 (negatives use inset shadows) ex: @include depth(5); @include depth(-3)
 */
menu {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  padding: 0 !important;
  -webkit-padding-start: 0;
  width: 28rem;
  height: 12rem;
  text-align: center;
  color: #fff;
  user-select: none;
  z-index: 99; }
  @media screen and (max-width: 47.9375em) {
    menu {
      top: 2rem;
      bottom: auto;
      width: 100%;
      height: auto; } }
  html.touch body[data-viewer-schema="video"] menu {
    pointer-events: none; }
  menu .burger {
    position: absolute;
    top: 0.2rem;
    right: 2.5rem;
    height: 0.6rem;
    display: none; }
    @media screen and (max-width: 47.9375em) {
      menu .burger {
        display: block; } }
    menu .burger span {
      position: absolute;
      top: 0;
      right: 0;
      font-family: 'UniversExt', Helvetica, sans-serif;
      font-weight: 400;
      font-size: 1.2rem; }
      menu .burger span:nth-child(2) {
        top: 50%; }
      menu .burger span:nth-child(3) {
        top: 100%; }
  menu .menu-wrapper {
    transition: opacity 500ms; }
    html:not(.touch) body.idle menu .menu-wrapper:not(.show-menu) {
      opacity: 0; }
    html.touch body[data-viewer-schema="video"] menu .menu-wrapper {
      opacity: 0 !important; }
    menu .menu-wrapper.show-menu h1, menu .menu-wrapper.show-menu .burger {
      opacity: 0;
      transition-delay: 0s; }
    menu .menu-wrapper.show-menu .table {
      opacity: 1;
      transition-delay: 0.2s; }
    menu .menu-wrapper.show-menu:not(.ng-animate) .table {
      pointer-events: auto; }
  menu .table {
    position: absolute;
    top: 0;
    left: 0; }
    @media screen and (max-width: 47.9375em) {
      menu .table .cell {
        vertical-align: top; } }
  menu h1, menu .table, menu .burger {
    transition: opacity 0.2s 0.2s; }
  menu h1 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    line-height: 12rem; }
    @media screen and (max-width: 47.9375em) {
      menu h1 {
        line-height: 3rem; } }
  menu .table {
    opacity: 0;
    transition-delay: 0s;
    pointer-events: none; }
  menu h1, menu li {
    letter-spacing: 0.15em; }
    html.touch menu h1, html.touch menu li {
      text-shadow: 0 0 15px rgba(0, 0, 0, 0.2); }
  menu li {
    line-height: 3rem; }
    @media screen and (max-width: 47.9375em) {
      menu li {
        margin: 0 0.5rem; } }
    @media screen and (min-width: 90em) {
      menu li {
        line-height: 3.5rem; } }
    menu li.active * {
      border-bottom: 1px solid currentColor; }
  [ui-view="overlay"][data-state*="index.portfolio"] menu li:nth-child(1),
  [ui-view="overlay"][data-state*="viewer"] menu li:nth-child(1) {
    display: none; }
  [ui-view="overlay"]:not([data-state*="viewer"]) menu li:nth-child(2) {
    display: none; }



